import api from '../../../api/apiConfig'
import uniIcons from '../../../components/uni-icons/uni-icons.vue'
import popUp from '../../../components/pop-up/pop-up.vue'
export default{
	data(){
		return{
			photoDomain : 'https://shop.itaojia.com/img/Taojia',
			form:{},
			noteText:'获取验证码',
			isNote:false,
			totalTime:60,
			smsVerificationCode:'',
		}
	},
	created() {
		const getUser = uni.getStorageSync('user')
		if(getUser){
			this.form = JSON.parse(getUser)
		}
		console.log(this.form)
	},
	components:{
		uniIcons,
		popUp
	},
	methods:{
		showPopUp(){
			this.$refs.popUp.handleShowShare()
		},
	  async	unbundle(){
		  let msg ={};
		  msg.phone = this.form.phone;
		  msg.smsVerificationCode = this.smsVerificationCode
		  console.log(msg)
		   const res = await api.unbindPhone(msg)
		   if(res.code === 200){
			    uni.removeStorageSync('user');
				uni.removeStorageSync('token');
				this.$refs.popUp.handleHiddenShare()
			   uni.showToast({
			   	title:'解绑成功',
				icon:'none',
				duration:2000
			   })
			   setTimeout(()=>{
				   uni.navigateTo({
				   	url:'/pages/user/login'
				   })
			   },1000)
			 
		   }else{
			   uni.showToast({
			   	title:'解绑失败',
				icon:'none',
				duration:2000
			   })
		   }
			
		},
		
		//短信验证处理
		async getNote(){
		  this.noteText = '发送中...'
		  console.log(this.form.phone)
		  let res = await api.getSmsVerificationCode({phone:this.form.phone})
		  console.log(res)
		  if(res.code === 200){
				this.isNote = true
			    this.noteText = '重新获取'+ this.totalTime+'s';
				let timer = setInterval(() => {
				this.totalTime--;
					this.noteText = '重新获取'+ this.totalTime+'s';
				if(this.totalTime === 0){
				  clearInterval(timer)
				  this.noteText = '获取验证码';
				  this.totalTime = 60;
				  this.isNote = false;
				}
				}, 1000);
		  }else{
			  uni.showToast({
			    title: res.message,
			    icon: "none",
			    duration: 3000
			  });
		  }
		},
		
		
	}
}
